


















































































@import "../../styles/variables";

.History {
  &__icon {
    a {
      color: #FFFFFF;
    }
  }

  &__sidebar {
    $sidebar-width: 30em;

    overflow-y: scroll;
    position: absolute;
    top: 0;
    right: 0;
    height: 100vh;
    width: $sidebar-width;
    background-color: $background-secondary;
    padding: 2em 0;
    animation-name: open-sidebar;
    animation-duration: 0.5s;

    @keyframes open-sidebar {
      from {
        right: -$sidebar-width;
      }
      to {
        right: 0;
      }
    }

    &__close {
      display: flex;
      justify-content: space-between;
      font-size: 2em;
      padding: 0 1em;

      h2 {
        font-size: 1em;
      }
    }

    @media (max-width: 480px) {
      width: 100%;
    }
  }
}
