

































@import "../../styles/variables";

.History-entry {
  width: 100%;
  padding: 1em 2em 0 2em;

  &:first-child {
    padding: 0 2em 0 2em;
  }

  &:hover {
    cursor: pointer;
    background-color: $background-primary;
  }

  &__content {
    display: flex;
  }

  &__image {
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 100%;
    }
  }

  &__title {
    width: 75%;
    padding-left: 1em;

    &__english {
      font-weight: bold;
      font-size: 1.2em;
    }

    &__japanese {
      font-size: 1em;
    }
  }

  &__divider {
    margin-top: 1em;
    border-bottom: #FFFFFF solid 0.1em;
    width: 100%;
  }
}
